'use client';
import { useState } from 'react';

export type RelationshipTypeOption = 'parent' | 'child' | 'spouse' | 'sibling';

export interface MedicalCondition {
    id: string;
    name: string;
    notes?: string;
}

export interface Person {
    id: string;
    firstName: string;
    lastName: string;
    gender?: string;
    birthDate?: string;
    deathDate?: string;
    avatarUrl?: string;
    medicalConditions?: MedicalCondition[];
}

export interface Union {
    id: string;
    partner1Id: string;
    partner2Id: string;
}

export function useFamilyTree() {
    const [persons, setPersons] = useState<Person[]>([]);
    const [unions, setUnions] = useState<Union[]>([]);
    const [graphData, setGraphData] = useState<any>({ nodes: [], edges: [], persons: [], unions: [] });
    const [isLoading, setIsLoading] = useState(false);

    const createPersonWithRelationship = async (
        personOptions: Partial<Person>,
        relatedPersonId: string,
        relationshipType: RelationshipTypeOption
    ) => {
        return { success: true };
    };

    const getRootPerson = () => {
        return persons.length > 0 ? persons[0] : null;
    };

    const getPersonRole = (personId: string) => {
        return 'relative';
    };

    const updatePerson = async (personId: string, updates: Partial<Person>) => {
        return { success: true };
    };

    const deletePerson = async (personId: string) => {
        return { success: true };
    };

    const getParentsOfPerson = (personId: string): Person[] => {
        return [];
    };

    return {
        persons,
        unions,
        graphData,
        isLoading,
        createPersonWithRelationship,
        getRootPerson,
        getPersonRole,
        updatePerson,
        deletePerson,
        getParentsOfPerson
    };
}
